import React, { Suspense } from 'react';
import { RouteObject } from 'react-router-dom';
import { Box, CircularProgress } from '@mui/material';

// Layouts
import MainLayout from './layouts/MainLayout';

//    app-routes    .
import { menuGroups, MenuItem } from './app-routes';

//    
const Loading = () => (
    <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', height: '100%' }}>
        <CircularProgress />
    </Box>
);

//            
const flattenMenuItems = (items: MenuItem[]): MenuItem[] => {
    const flatList: MenuItem[] = [];
    items.forEach(item => {
        // children    (   )
        if (item.path && item.component) {
            flatList.push(item);
        }
        // children      
        if (item.children) {
            flatList.push(...flattenMenuItems(item.children));
        }
    });
    return flatList;
};

//         .
const allPages = menuGroups.flatMap(group => flattenMenuItems(group.items));

//     react-router-dom RouteObject  .
const childRoutes: RouteObject[] = allPages
    .filter(page => page.path && page.component) //      
    .map(page => {
        const PageComponent = page.component!;
        return {
            // path   '/' .
            path: page.path!.substring(1),
            element: (
                <Suspense fallback={<Loading />}>
                    <PageComponent />
                </Suspense>
            ),
        };
    });

export const routesConfig: RouteObject[] = [
    {
        path: '/',
        element: <MainLayout />,
        //      .
        children: childRoutes,
    },
];