import 'pretendard/dist/web/static/pretendard.css';
import { createTheme, ThemeOptions } from '@mui/material/styles';
import { PaletteMode } from '@mui/material';

// TypeScript       
declare module '@mui/material/styles' {
    // Palette PaletteOptions   .
    interface Palette {
        charts: {
            main: string;
            secondary: string;
            axis: string;
        };
    }
    interface PaletteOptions {
        charts?: {
            main?: string;
            secondary?: string;
            axis?: string;
        };
    }
}

//     .
const commonSettings = (mode: PaletteMode): ThemeOptions => ({
    typography: {
        fontFamily: [
            'Pretendard',
            '-apple-system',
            'BlinkMacSystemFont',
            'system-ui',
            'Roboto',
            '"Helvetica Neue"',
            '"Apple SD Gothic Neo"',
            '"Noto Sans KR"',
            'sans-serif',
        ].join(','),
        h1: {
            fontSize: '2.25rem',
            fontWeight: 600,
        },
        button: {
            textTransform: 'none', //     
        },
    },
    components: {
        MuiCssBaseline: {
            styleOverrides: (theme) => ({
                body: {
                    //       .
                    scrollbarColor: `${theme.palette.mode === 'dark' ? theme.palette.grey[800] : theme.palette.grey[400]} ${theme.palette.background.default}`,
                    '&::-webkit-scrollbar, & *::-webkit-scrollbar': {
                        backgroundColor: 'transparent',
                        width: '8px',
                    },
                    '&::-webkit-scrollbar-thumb, & *::-webkit-scrollbar-thumb': {
                        borderRadius: 8,
                        backgroundColor: theme.palette.mode === 'dark' ? theme.palette.grey[800] : theme.palette.grey[400],
                        minHeight: 24,
                    },
                    '&::-webkit-scrollbar-thumb:focus, & *::-webkit-scrollbar-thumb:focus': {
                        backgroundColor: theme.palette.mode === 'dark' ? theme.palette.grey[700] : theme.palette.grey[500],
                    },
                    '&::-webkit-scrollbar-thumb:active, & *::-webkit-scrollbar-thumb:active': {
                        backgroundColor: theme.palette.mode === 'dark' ? theme.palette.grey[700] : theme.palette.grey[500],
                    },
                    '&::-webkit-scrollbar-thumb:hover, & *::-webkit-scrollbar-thumb:hover': {
                        backgroundColor: theme.palette.mode === 'dark' ? theme.palette.grey[700] : theme.palette.grey[500],
                    },
                },
            }),
        },
        MuiAccordion: {
            styleOverrides: {
                root: ({ theme }) => ({
                    border: `1px solid ${theme.palette.divider}`,
                    '&:not(:first-of-type)': {
                        borderTop: 'none',
                    },
                    '&.Mui-expanded': {
                        margin: 0,
                    },
                    boxShadow: 'none',
                    '&:first-of-type': {
                        borderTopLeftRadius: 0,
                        borderTopRightRadius: 0,
                    },
                    '&:last-of-type': {
                        borderBottomLeftRadius: 0,
                        borderBottomRightRadius: 0,
                    },
                }),
            },
        },
        MuiAppBar: {
            styleOverrides: {
                root: {
                    boxShadow: 'none',
                },
            },
        },
        MuiPaper: {
            styleOverrides: {
                root: {
                    // Paper       
                    boxShadow: '0 1px 3px rgba(0,0,0,0.12), 0 1px 2px rgba(0,0,0,0.24)',
                },
            },
        },
        //  [] DataGrid   .
        MuiDataGrid: {
            styleOverrides: {
                root: ({ theme }) => ({
                    // DataGrid    paper  .
                    '--DataGrid-t-color-background-base': theme.palette.background.paper,
                    //     .
                    border: `1px solid ${theme.palette.divider}`,
                }),
            },
        },
    },
    spacing: 4, //    (: theme.spacing(2) === 8px)
});

//    
const lightPalette: ThemeOptions['palette'] = {
    mode: 'light',
    primary: {
        main: '#323F53',
    },
    background: {
        default: '#f4f6f8',
        paper: '#ffffff',
    },
    text: {
        primary: '#1A2027',
        secondary: '#3E5060',
    },
    divider: 'rgba(0, 0, 0, 0.12)',
    charts: {
        main: '#323F53',
        secondary: '#82aaff',
        axis: '#637381',
    },
};

//    
const darkPalette: ThemeOptions['palette'] = {
    mode: 'dark',
    primary: {
        main: '#A8B0BC',
    },
    background: {
        default: '#121212',
        paper: '#1e1e1e',
    },
    text: {
        primary: '#E0E3E7',
        secondary: '#B0B8C4',
    },
    divider: 'rgba(255, 255, 255, 0.12)',
    charts: {
        main: '#A8B0BC',
        secondary: '#5c85d6',
        axis: '#919EAB',
    },
};

//    
export const getTheme = (mode: PaletteMode) => {
    const palette = mode === 'light' ? lightPalette : darkPalette;
    return createTheme({
        ...commonSettings(mode),
        palette,
    });
};