// src/contexts/SnackbarProvider.tsx
import React, { useState, createContext, useContext, useCallback } from 'react';
import { Snackbar, Alert, AlertColor } from '@mui/material';

//     
type ShowSnackbarFunction = (message: string, severity?: AlertColor) => void;

// Context 
const SnackbarContext = createContext<ShowSnackbarFunction | undefined>(undefined);

/**
 *           .
 * : const showSnackbar = useSnackbar(); showSnackbar('!', 'success');
 */
export const useSnackbar = () => {
    const context = useContext(SnackbarContext);
    if (context === undefined) {
        throw new Error('useSnackbar must be used within a SnackbarProvider');
    }
    return context;
};

// Provider 
export const SnackbarProvider: React.FC<{ children: React.ReactNode }> = ({ children }) => {
    const [open, setOpen] = useState(false);
    const [message, setMessage] = useState('');
    const [severity, setSeverity] = useState<AlertColor>('info');

    const handleClose = (event?: React.SyntheticEvent | Event, reason?: string) => {
        if (reason === 'clickaway') {
            return;
        }
        setOpen(false);
    };

    const showSnackbar: ShowSnackbarFunction = useCallback((msg, sev = 'info') => {
        setMessage(msg);
        setSeverity(sev);
        setOpen(true);
    }, []);

    return (
        <SnackbarContext.Provider value={showSnackbar}>
            {children}
            <Snackbar
                open={open}
                autoHideDuration={4000} // 4   
                onClose={handleClose}
                anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}
            >
                {/* Alert  severity       UX . */}
                <Alert onClose={handleClose} severity={severity} sx={{ width: '100%' }}>
                    {message}
                </Alert>
            </Snackbar>
        </SnackbarContext.Provider>
    );
};